import { DatePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Note } from '../models/Note';
import { MynotesService } from './mynotes.service';

@Component({
  selector: 'app-my-notes',
  templateUrl: './my-notes.component.html',
  styleUrls: ['./my-notes.component.css']
})
export class MyNotesComponent implements OnInit {

  notes: Note[];
  note: Note;
  isCreated: boolean;

  constructor(private _mynotesService: MynotesService, private datePipe: DatePipe) { }

  ngOnInit() {
    this.getNotes();
  }

  getNotes() {
    this._mynotesService.getNotes().subscribe(
      response => {
        this.notes = response;
      },
      responseError => {
        this.notes = null;
        console.log(responseError);
      }
    );
  }

  createNote(note) {
    this._mynotesService.postNote(note).subscribe(
      response => {
        if (response != null) {
          this.isCreated = true;
        }
        else {
          this.isCreated = false;
        }
      }
    );
  }

  formatDate(value) {
    const date = new Date(value);
    return this.datePipe.transform(date, 'dd/MM/yyyy');
  }
}
